import React from "react";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  MenuItem,
  Box
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { StyledSelect } from '../styles';

function MethodSelection({
  stakcoMethod,
  onMethodSelected,
  stakcoMethodOptions,
  expanded
}) {
  return (
    <Accordion sx={{ width: 1 }} expanded={expanded}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel2-content"
        id="panel2-header"
        sx={{
          flexDirection: "row-reverse",
          justifyContent: "space-between",
          height: 70,
        }}
      >
        Stage 1 - Method
      </AccordionSummary>
      <AccordionDetails sx={{ height: "auto", display: "flex", flexDirection: "column" }}>
        <Box sx={{ flexGrow: 1 }} />
        <StyledSelect
          variant="filled"
          fullWidth
          value={stakcoMethod}
          label="Method"
          onChange={onMethodSelected}
        >
          {stakcoMethodOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </StyledSelect>
      </AccordionDetails>
    </Accordion>
  );
}

export default MethodSelection;
