import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Typography, Button } from '@mui/material';
import { redirectToAuthorizationEndpoint, handleAuthorizationCallback } from './oauthUtils';

const Login = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const authorizationCode = urlParams.get('code');
    if (authorizationCode) {
      handleAuthorizationCallback(authorizationCode)
        .then(success => {
          if (success) {
            const username = localStorage.getItem('username');
            navigate('/find', { state: { username } });
          }
        })
        .catch(error => {
          console.error("Login Failure:", error);
        });
    }
  }, [navigate]);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginTop: '50px' }}>
      <Typography variant="h4" gutterBottom>
        Welcome to Stakco Studio
      </Typography>
      <Button variant="contained" 
        color="primary" onClick={redirectToAuthorizationEndpoint}
        sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}

      >
        Login with Google
      </Button>
    </Box>
  );
};

export default Login;
