import React, { useEffect, useState, useCallback } from "react";
import { useLocation } from 'react-router-dom';
import "./find.css";
import { Box, Button, CircularProgress, Grid } from "@mui/material";
import { Download as DownloadIcon, Launch as LaunchIcon } from "@mui/icons-material";
import StakcoAppBar from "./../../components/StakcoAppBar";

const stakco_backend_url = "https://stakco.uc.r.appspot.com/api/stakco";
const stakco_images_url = "https://storage.googleapis.com/stakco-images";

function Find() {
  const location = useLocation();
  const storedUsername = localStorage.getItem('username');
  const { username } = location.state || { username: storedUsername };

  const [projects, setProjects] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (!username) {
      console.error("Username is not provided");
      return;
    }

    const fetchProjects = async () => {
      setIsLoading(true);
      try {
        const response = await fetch(`${stakco_backend_url}/${username}`);
        const data = await response.json();
        setProjects(data);
      } catch (error) {
        console.error("Error fetching projects:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchProjects();
  }, [username]);

  const handleDownloadLayer = useCallback((folderName) => {
    const imageUrl = `${stakco_images_url}/${username}/${folderName}/${folderName.split('_')[0]}_1.png`;
    fetch(imageUrl, { method: 'GET', mode: 'cors' })
      .then((response) => response.blob())
      .then((blob) => {
        const a = document.createElement('a');
        a.href = URL.createObjectURL(blob);
        a.download = `${folderName}_1.png`;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      })
      .catch((error) => console.error('Error downloading image:', error));
  }, [username]);

  const handleOpenProject = useCallback((folderName) => {
    const projectUrl = `${stakco_images_url}/${username}/${folderName}/${folderName.split('_')[0]}_1.png`;
    window.open(projectUrl, '_blank', 'noopener,noreferrer');
  }, [username]);

  return (
    <div className="stakco-app">
      <StakcoAppBar />

      <Box sx={{ margin: "100px 30px 30px 30px" }}>
        {isLoading ? (
          <CircularProgress />
        ) : (
          <Grid container spacing={2}>
            {projects.map((project, index) => (
              <Grid item xs={12} sm={6} md={2} key={index}>
                <div className="project-card">
                  <img
                    src={`${stakco_images_url}/${username}/${project}/${project.split('_')[0]}_1.png`}
                    alt={`Project ${index + 1}`}
                    className="project-image"
                  />
                  <div className="project-actions">
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<DownloadIcon />}
                      onClick={() => handleDownloadLayer(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      Download Layer
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<LaunchIcon />}
                      onClick={() => handleOpenProject(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      Open Project
                    </Button>
                  </div>
                </div>
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    </div>
  );
}

export default Find;
