import React, { useState, useCallback } from "react";
import { Accordion, AccordionSummary, AccordionDetails, Grid, Box, IconButton, Card, MenuItem, Stack, Button } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import DeleteIcon from "@mui/icons-material/Delete";
import LeftIcon from "@mui/icons-material/ArrowBackIos";
import RightIcon from "@mui/icons-material/ArrowForwardIos";
import ImageButton from "./ImageButton";

import { StyledSelect } from '../styles';

function StencilStage({
  stageIndex,
  stakcoStencilOptions, // []
  onStencilImageUploaded, // callback
  onStencilSelected, // callback
  onRemoveButtonPress, // callback
}) {
  const [selectedStencilIndex, setSelectedStencilIndex] = useState(0);
  const defaultImage = process.env.PUBLIC_URL + "/selectStencil.jpg";


  const handlePrevClick = useCallback(() => {
    setSelectedStencilIndex((prevIndex) => {
      const newIndex = prevIndex === 0 ? stakcoStencilOptions.length - 1 : prevIndex - 1;
      onStencilSelected(stakcoStencilOptions[newIndex], stageIndex);
      return newIndex;
    });
  }, [stakcoStencilOptions, stageIndex, onStencilSelected]);

  const handleNextClick = useCallback(() => {
    setSelectedStencilIndex((prevIndex) => {
      const newIndex = prevIndex === stakcoStencilOptions.length - 1 ? 0 : prevIndex + 1;
      onStencilSelected(stakcoStencilOptions[newIndex], stageIndex);
      return newIndex;
    });
  }, [stakcoStencilOptions, stageIndex, onStencilSelected]);

  const handleSelectChange = useCallback((event) => {
    const newIndex = stakcoStencilOptions.findIndex(option => option.value === event.target.value);
    setSelectedStencilIndex(newIndex);
    onStencilSelected(stakcoStencilOptions[newIndex], stageIndex);
  }, [stakcoStencilOptions, stageIndex, onStencilSelected]);

  const handleFileUpload = useCallback((event)=>{
    onStencilImageUploaded(event, stageIndex);
    setSelectedStencilIndex(stakcoStencilOptions.length);
  }, [stakcoStencilOptions.length, stageIndex, onStencilImageUploaded]);

  return (
    <Accordion key={stageIndex}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel${stageIndex + 3}-stencil-content`}
        id={`panel${stageIndex + 3}-stencil-header`}
        sx={{
          flexDirection: "row-reverse",
          justifyContent: "space-between",
          marginBottom: 0,
          height: 70
        }}
      >
        <Grid container spacing={1}>
          <Grid item xs={11}>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                height: "100%",
              }}
            >
              {`Stage ${3 + stageIndex} - Stencil`}
            </Box>
          </Grid>
          <Grid item xs={1}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end",
                alignItems: "center",
                height: "100%",
              }}
            >
              <IconButton
                aria-label="delete"
                onClick={() => onRemoveButtonPress(stageIndex)}
              >
                <DeleteIcon />
              </IconButton>
            </Box>
          </Grid>
        </Grid>
      </AccordionSummary>
      <AccordionDetails
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          height: "auto",
          position: "relative",
        }}
      >
        <Stack direction="row" spacing={2}>
          <Button startIcon={<LeftIcon />} onClick={handlePrevClick}></Button>
          <Card variant="outlined" sx={{ width: 200, height: 200, border: 'none !important'  }}>
            <ImageButton
              url={stakcoStencilOptions[selectedStencilIndex]?.thumb || defaultImage}
              title="Stencil"
              size={200}
              onFileChange={handleFileUpload}
            />
          </Card>
          <Button startIcon={<RightIcon />} onClick={handleNextClick}></Button>
        </Stack>
        <Box sx={{ flexGrow: 1 }} /> 

        <StyledSelect
          fullWidth
          variant="filled"
          value={stakcoStencilOptions[selectedStencilIndex]?.value || ""}
          label="Stencil"
          onChange={handleSelectChange}
          sx={{ marginTop: 2 }}
        >
          {stakcoStencilOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </StyledSelect>


      </AccordionDetails>
    </Accordion>
  );
}

export default StencilStage;
