import React, { useState, useRef, useEffect, useCallback } from "react";
import "./create.css";
import { Box, Button, CircularProgress } from "@mui/material";
import { Add as AddIcon, Download as DownloadIcon, Launch as LaunchIcon } from "@mui/icons-material";
import StakcoAppBar from "./../../components/StakcoAppBar";
import StencilStage from "./../../components/StencilStage";
import MethodSelection from "./../../components/MethodSelection";
import InputSelection from "./../../components/InputSelection";
import ResultsBlock from "./../../components/ResultsBlock";
import LayerSelection from "./../../components/LayerSelection";

const stakcoMethodOptions = [
  { value: "Zero", label: "Stakco Zero" },
  { value: "Neo", label: "Stakco Neo" },
  { value: "Geko", label: "Stakco Geko" },
];

const stakco_unity_app_url = process.env.REACT_APP_STAKCO_UNITY_APP_URL;

const stakco_backend_url = "https://stakco.uc.r.appspot.com/api/stakco"; //process.env.REACT_APP_STAKCO_BACKEND_URL;//"https://stakco.uc.r.appspot.com/api/stakco"; // 

function Create() {
  const [inputImage, setInputImage] = useState(null);
  const [inputImageName, setInputImageName] = useState("");
  const [stakcoMethod, setStakcoMethod] = useState("Neo");
  const [stages, setStages] = useState([1]);
  const [stakcoStencil, setStakcoStencil] = useState([0]);
  const [stakcoStencilOptions, setStakcoStencilOptions] = useState([]);
  const [stakcoLayers, setStakcoLayers] = useState([]);
  const [showResults, setShowResults] = useState(false);
  const [showQR, setShowQR] = useState(false);
  const resultsBlockRef = useRef();

  const [deployedPuzzleUrl, setDeployedPuzzleURL] = useState(stakco_unity_app_url);

  const [isLoading, setIsLoading] = useState(false);

  const [allStakcosLoaded, setAllStakcosLoaded] = useState(false);

  const [layerCount, setLayerCount] = useState(3);
  const [imageBlades, setImageBlades] = useState([[60, 60, 60], [120, 120, 120], [240, 240, 240]]);

  // eslint-disable-next-line
  const username = localStorage.getItem("username");
  const [puzzleId, setPuzzleId] = useState();

  useEffect(() => {
    const loadStencils = async () => {
      const context = require.context('../../../public/stencils', false, /\.(png|jpe?g|svg)$/);
      const options = await Promise.all(
        context.keys().map(async (key, index) => {
          const fileName = key.replace('./', '');
          const label = fileName.replace(/\.[^/.]+$/, "");
          const imageURL = `${process.env.PUBLIC_URL}/stencils/${fileName}`;
          const thumbimageURL = `${process.env.PUBLIC_URL}/stencils/thumbs/${fileName}`;
          const image = await fetchLocalImage(imageURL);
          const thumb = await fetchLocalImage(thumbimageURL);
          return {
            value: index + 1,
            label: label,
            image: image,
            thumb: thumb
          };
        })
      );
      setStakcoStencilOptions(options);

      setStakcoStencil((prevStencils) => {
        // If prevStencils is empty, initialize with the default option
        if (prevStencils.length === 0) {
          return options.map(() => options[0]);
        }
        // Otherwise, map over the existing stencils
        return prevStencils.map((stencil, index) => stencil || options[0]);
      });
      /////
    };

    loadStencils();
  }, []);

  const fetchLocalImage = async (url) => {
    const response = await fetch(url);
    const blob = await response.blob();
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
      reader.onloadend = () => resolve(reader.result);
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
  };

  const handleDownloadQR = useCallback(() => {
    if (resultsBlockRef.current) {
      const canvas = resultsBlockRef.current.querySelector('canvas');
      if (canvas) {
        const qrImage = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = qrImage;
        link.download = 'stakco-qr-code.png';
        link.click();
      }
    }
  }, []);

  const handleAddStage = useCallback(() => {
    setStages((prevStages) => [...prevStages, {}]);
    setStakcoStencil((prevStencils) => [...prevStencils, stakcoStencilOptions[0]]);
  }, [stakcoStencilOptions]);

  const donwloadAllStakcoLayers = useCallback(() => {
    stakcoLayers.forEach((url, index) => {
      fetch(url, { method: 'GET', mode: 'cors' })
        .then((response) => response.blob())
        .then((blob) => {
          const a = document.createElement('a');
          a.href = URL.createObjectURL(blob);
          a.download = `stakco_layer_${index + 1}.png`;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        })
        .catch((error) => console.error('Error downloading image:', error));
    });
  }, [stakcoLayers]);

  const deployStakco = useCallback(() => {
    setShowQR(true);
  }, []);

  // const initiateGeneration = useCallback(() => {
  //   if (!inputImage || !stakcoStencil.every((stencil) => stencil)) {
  //     console.error("Input image or stencil images are missing.");
  //     return;
  //   }
  
  //   setIsLoading(true); // Start loading
  
  //   const base64ToBlob = (base64, mimeType) => {
  //     const byteString = atob(base64.split(",")[1]);
  //     const ab = new ArrayBuffer(byteString.length);
  //     const ia = new Uint8Array(ab);
  //     for (let i = 0; i < byteString.length; i++) {
  //       ia[i] = byteString.charCodeAt(i);
  //     }
  //     return new Blob([ab], { type: mimeType });
  //   };
  //   const formData = new FormData();
  //   formData.append("username", username);

  //   formData.append("image1", base64ToBlob(inputImage, "image/png"), "image1.png");
  
  //   stakcoStencil.forEach((stencil, index) => {
  //     formData.append(`stencilImages`, base64ToBlob(stencil.image, "image/png"), `stencilImage${index + 1}.png`);
  //   });
  
  //   formData.append("layers", layerCount.toString());

  //   formData.append("colors", JSON.stringify([[255, 0, 0], [0, 255, 0], [0, 0, 255]]));
  //   formData.append("blades", JSON.stringify(imageBlades));
  
  //   fetch(stakco_backend_url, {
  //     method: "POST",
  //     body: formData,
  //     timeout: 5000
  //   })
  //     .then((response) => {
  //       if (!response.ok) {
  //         throw new Error("Failed to generate Stakcos.");
  //       }
  //       return response.json();
  //     })
  //     .then((data) => {
  //       setShowResults(true);
  //       setStakcoLayers(data);
  //       const puzzleId = data[0].split('/')[5];
  //       setPuzzleId(puzzleId);
  //       console.log(puzzleId);
  //       console.log("Stakcos generated successfully:", data);
  //     })
  //     .catch((error) => {
  //       console.error("Error generating Stakcos:", error.message);
  //     })
  //     .finally(() => {
  //       setIsLoading(false); // Stop loading
  //     });
  // }, [inputImage, stakcoStencil, layerCount]);

  const initiateGeneration = useCallback(() => {
    if (!inputImage || !stakcoStencil.every((stencil) => stencil)) {
      console.error("Input image or stencil images are missing.");
      return;
    }

    setIsLoading(true); // Start loading

    const formData = new FormData();
    formData.append("username", username);

    formData.append("image1", inputImage);

    stakcoStencil.forEach((stencil, index) => {
      formData.append("stencilImages", stencil.image);
    });

    formData.append("layers", layerCount.toString());
    formData.append("colors", JSON.stringify([[255, 0, 0], [0, 255, 0], [0, 0, 255]]));
    formData.append("blades", JSON.stringify(imageBlades));
    formData.append("method", stakcoMethod);

    fetch(stakco_backend_url, {
      method: "POST",
      body: formData
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to generate Stakcos.");
        }
        return response.json();
      })
      .then((data) => {
        setShowResults(true);
        setStakcoLayers(data);
        const puzzleId = data[0].split('/')[5];
        setPuzzleId(puzzleId);
        setDeployedPuzzleURL(`${stakco_unity_app_url}?username=${username}&puzzle=${puzzleId}`);
      })
      .catch((error) => {
        console.error("Error generating Stakcos:", error.message);
      })
      .finally(() => {
        setIsLoading(false); // Stop loading
      });
  }, [inputImage, stakcoStencil, layerCount, imageBlades, username, stakcoMethod]);

  

  const openStakco = useCallback(() => {
    if (!puzzleId) {
      console.error("Puzzle ID is undefined");
      return;
    }
    let puzzle_location = deployedPuzzleUrl;
    window.open(puzzle_location, '_blank', 'noopener,noreferrer');
  }, [puzzleId, deployedPuzzleUrl]);

  const handleRemoveStage = useCallback((index) => {
    setStages((prevStages) => prevStages.filter((_, i) => i !== index));
    setStakcoStencil((prevStencils) => prevStencils.filter((_, i) => i !== index));
  }, []);

  const handleSelectedStencil = useCallback((stencil, stageIndex) => {
    setStakcoStencil((prevStencils) => {
      const newStencils = [...prevStencils];
      newStencils[stageIndex] = stencil;
      return newStencils;
    });
  }, []);

  const handleUploadedStencilImage = (event, stageIndex) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = (e) => {
      const newStencilIndex = stakcoStencilOptions.length;
  
      const newStencil = {
        value: newStencilIndex + 1,
        label: file.name,
        image: e.target.result,
        thumb: e.target.result
      };
  
      setStakcoStencilOptions((prevState) => [...prevState, newStencil]);
  
      setStakcoStencil((prevStencils) => {
        const newStencils = [...prevStencils];
        newStencils[stageIndex] = newStencil;
        return newStencils;
      });
    };
    reader.readAsDataURL(file);
  };

  const handleSelectedInput = useCallback((event) => {
    const file = event.target.files[0];
    const reader = new FileReader();
  
    reader.onload = (e) => {
      const img = new Image();
      img.onload = () => {
        const resolution = `${img.width}x${img.height}`;
        setInputImage(e.target.result);
        setInputImageName(`: ${file.name} (${resolution})`);
      };
      img.src = e.target.result;
    };
  
    reader.readAsDataURL(file);
  }, []);
  
  const handleLayerCountChanged = useCallback((newLayerCount) => {
    setLayerCount(newLayerCount);
    const maxVal = 250;
    const step = Math.round(maxVal / newLayerCount);
    const newImageBlades = [];

    for (let i = 1; i <= newLayerCount; i++) {
      if (i === newLayerCount) {
        newImageBlades.push([maxVal, maxVal, maxVal]);
      } else {
        const val = step * i;
        newImageBlades.push([val, val, val]);
      }
    }
    setImageBlades(newImageBlades);
  }, []);

  const handleSelectedMethod = useCallback((event) => {
    setStakcoMethod(event.target.value);
  }, []);

  return (
    <div className="stakco-app">
      <StakcoAppBar />

      <Box sx={{ margin: "100px 30px 30px 30px" }}>
        <InputSelection image={inputImage} name={inputImageName} onInputImageSelected={handleSelectedInput} />

        <MethodSelection
          stakcoMethod={stakcoMethod}
          stakcoMethodOptions={stakcoMethodOptions}
          onMethodSelected={handleSelectedMethod}
        />

        <LayerSelection onLayerCountChanged={handleLayerCountChanged} /> 

        {stages.map((stage, stageIndex) => (
          <StencilStage
            key={stageIndex}
            stageIndex={stageIndex}
            stakcoStencilOptions={stakcoStencilOptions}
            onStencilImageUploaded={(event, index) => handleUploadedStencilImage(event, index)}
            onStencilSelected={(stencil, stageIndex) => handleSelectedStencil(stencil, stageIndex)}
            onRemoveButtonPress={() => handleRemoveStage(stageIndex)}
          />
        ))}

        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={handleAddStage}
          fullWidth
          sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
        >
          Add Stage
        </Button>

        <Button
          variant="contained"
          color="primary"
          onClick={initiateGeneration}
          disabled={!inputImage || isLoading}
          fullWidth
          sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" }, position: 'relative' }}
        >
          Generate Stakcos
          {isLoading && <CircularProgress size={24} sx={{ position: 'absolute', top: '50%', right: '25px', marginTop: '-12px', marginLeft: '-12px' }} />}
        </Button>

      </Box>

      {showResults && (
        <Box sx={{ margin: "30px 30px 30px 30px" }}>

          <ResultsBlock 
            ref={resultsBlockRef} 
            stakcos={stakcoLayers} 
            qrURL={deployedPuzzleUrl} 
            showQR={showQR} 
            onAllStakcosLoaded={setAllStakcosLoaded} 
          />

          <Button
            disabled={showQR}
            variant="contained"
            color="primary"
            onClick={deployStakco}
            fullWidth
            sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
          >
            Deploy Stakco
          </Button>

          <Button
            variant="contained"
            color="primary"
            startIcon={<DownloadIcon />}
            onClick={donwloadAllStakcoLayers}
            fullWidth
            sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
            disabled={!allStakcosLoaded}
          >
            Download Layers
          </Button>

          <Button
            disabled={!showQR}
            variant="contained"
            color="primary"
            startIcon={<DownloadIcon />}
            onClick={handleDownloadQR}
            fullWidth
            sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
          >
            Download QR
          </Button>

          <Button
            disabled={!showQR}
            variant="contained"
            color="primary"
            onClick={openStakco}
            startIcon={<LaunchIcon />}
            fullWidth
            sx={{ marginTop: "20px", height: 70, backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
          >
            Open Stakco
          </Button>
        </Box>
      )}
    </div>
  );
}

export default Create;
