import React, { useState, useEffect } from "react";
import { Accordion, AccordionSummary, AccordionDetails, Box, IconButton, Typography } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import RemoveCircleIcon from "@mui/icons-material/RemoveCircle";

function LayerSelection({ onLayerCountChanged, expanded }) {
  const [layerCount, setLayerCount] = useState(3);

  const handleAddLayer = () => {
    const newLayerCount = layerCount + 1;
    setLayerCount(newLayerCount);
    onLayerCountChanged(newLayerCount);
  };

  const handleRemoveLayer = () => {
    if (layerCount > 2) {
      const newLayerCount = layerCount - 1;
      setLayerCount(newLayerCount);
      onLayerCountChanged(newLayerCount);
    }
  };

  useEffect(() => {
    onLayerCountChanged(layerCount);
  }, [layerCount, onLayerCountChanged]);

  return (
    <Accordion sx={{ width: 1 }} expanded={expanded}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel-layer-content"
        id="panel-layer-header"
        sx={{
          flexDirection: "row-reverse",
          justifyContent: "space-between",
          height: 70,
        }}
      >
        Stage 2 - Layers
      </AccordionSummary>
      <AccordionDetails
        sx={{ height: "auto", display: "flex", flexDirection: "column", alignItems: "center" }}
      >
        <Box sx={{ flexGrow: 1 }} />
        <Box sx={{ display: "flex", alignItems: "center", justifyContent: "center", width: "100%" }}>
          <IconButton size="large" onClick={handleRemoveLayer} disabled={layerCount <= 2} sx={{ color: 'orange'}}>
            <RemoveCircleIcon fontSize="large"/>
          </IconButton>
          <Typography sx={{ mx: 2, userSelect: 'none', pointerEvents: 'none' }}>{layerCount} Layers</Typography>
          {/* {layerCount} Layers */}
          <IconButton size="large" onClick={handleAddLayer} sx={{ color: 'orange'}}>
            <AddCircleIcon fontSize="large" />
          </IconButton>
        </Box>
      </AccordionDetails>
    </Accordion>
  );
}

export default LayerSelection;
