import React from "react";
import { Button } from "@mui/material";

function ImageButton({ url, title, size, onFileChange, disabled=false }) {
  const handleFileChange = (event) => {
    if (onFileChange) {
      onFileChange(event);
    }
  };

  return (
    <Button
      variant="contained"
      component="label"
      disabled={disabled}
      sx={{
        width: size,
        height: size,
        background: `url(${url}) no-repeat center/cover`,
        borderRadius: '50%',
        overflow: 'hidden', 
        padding: 0
      }}
    >
      <input type="file" hidden onChange={handleFileChange} />
    </Button>
  );
}

export default ImageButton;
