import React, { useState, useEffect, forwardRef } from "react";
import { Accordion, AccordionSummary, AccordionDetails, Card, Button, Stack, CircularProgress } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ImageButton from './ImageButton';
import QRCode from 'qrcode.react';
import LeftIcon from "@mui/icons-material/ArrowBackIos";
import RightIcon from "@mui/icons-material/ArrowForwardIos";

const ResultsBlock = forwardRef(({ stakcos, qrURL, showQR, onAllStakcosLoaded }, ref) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [loadedStakcos, setLoadedStakcos] = useState(Array(stakcos.length).fill(false));
  const handlePrevClick = () => {
    setCurrentIndex((prevIndex) => (prevIndex === 0 ? stakcos.length - 1 : prevIndex - 1));
  };

  const handleNextClick = () => {
    setCurrentIndex((prevIndex) => (prevIndex === stakcos.length - 1 ? 0 : prevIndex + 1));
  };

  useEffect(() => {
    const checkImages = () => {
      const promises = stakcos.map((url, index) => 
        new Promise((resolve) => {
          const img = new Image();
          img.src = url;
          img.onload = () => resolve({ index, loaded: true });
          img.onerror = () => resolve({ index, loaded: false });
        })
      );

      Promise.all(promises).then((results) => {
        const updatedLoadedStakcos = results.map((result) => result.loaded);
        setLoadedStakcos(updatedLoadedStakcos);

        if (updatedLoadedStakcos.includes(false)) {
          setTimeout(checkImages, 500); // Retry every 1 second
        } else {
          onAllStakcosLoaded(true); // Notify parent that all layers are loaded
        }
      });
    };

    checkImages();
  }, [stakcos, onAllStakcosLoaded]);

  return (
    <Accordion sx={{ width: 1 }} ref={ref}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel2-content"
        id="panel2-header"
        sx={{ flexDirection: "row-reverse", justifyContent: "space-between", height: 70 }}
      >
        Results - Generated Stakcos
        {stakcos[currentIndex]}
      </AccordionSummary>
      <AccordionDetails
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          height: "auto",
          position: "relative",
        }}
      >
        <Stack direction="row" spacing={2}>
          <Button onClick={handlePrevClick} startIcon={<LeftIcon/>}></Button>
          <div style={{ position: 'relative', width: 200, height: 200, border: 'none !important'  }}>
            <Card variant="outlined" sx={{ position: 'absolute', top: 0, left: 0, width: 200, height: 200, overflow: 'hidden', display: 'flex', alignItems: 'center', justifyContent: 'center', border: 'none !important'  }}>
              {loadedStakcos[currentIndex] ? (
                <ImageButton disabled={true} url={stakcos[currentIndex]} title="Stencil" size={200} />
              ) : (
                <CircularProgress />
              )}
            </Card>
            <Card variant="elevation" sx={{ position: 'absolute', top: 0, left: 0, width: 100, height: 200, boxShadow: 'none', overflow: 'hidden', backgroundColor: showQR ? 'white' : 'transparent' }}>
              {showQR && <QRCode size={200} value={qrURL} />}
            </Card>
          </div>
          <Button onClick={handleNextClick} startIcon={<RightIcon/>}></Button>
        </Stack>
      </AccordionDetails>
    </Accordion>
  );
});

export default ResultsBlock;
